export default {
	data() {
		return {
			height: 64, //header高度
			top: 0, //标题图标距离顶部距离
			scrollH: 0, //滚动总高度
			opcity: 0,
			iconOpcity: 0,
			chooseId:0,//选中的充值面额id
			value:1,
			rechargeList:[{id:1,money:58,give:60},{id:2,money:168,give:200},{id:3,money:398,give:500}],
			payShow:false,
			choosePayId:0,
			confirmShow:false,
			explainShow:false,
			totalMoney:0,
			totalGive:0,
			chooseMoney:0,
			chooseGive:0
		}
	},
	onLoad() {
		let obj = {};

		obj = wx.getMenuButtonBoundingClientRect();







				
		setTimeout(() => {
			uni.getSystemInfo({
				success: (res) => {
					this.width = obj.left || res.windowWidth;
					this.height = obj.top ? (obj.top + obj.height + 8) : (res.statusBarHeight + 44);
					this.top = obj.top ? (obj.top + (obj.height - 32) / 2) : (res.statusBarHeight + 6);
					this.scrollH = res.windowWidth
				}
			})
		}, 50)
	},
	methods: {
		valChange(e) {
			console.log(e);
			this.value = e.value
			this.totalMoney = this.chooseMoney * this.value
			this.totalGive = this.chooseGive * this.value
		},
		handleChooseMoney(id,money,give){
			this.chooseId = id
			this.chooseMoney = money
			this.chooseGive = give
			this.totalMoney = money * this.value
			this.totalGive = give * this.value
		},
		// 唤出支付方式
		handleToPay(){
			this.payShow = true
		},
		// 选中支付方式
		choosePayWay(id){
			this.choosePayId = id
		},
		// 支付
		handleToSettle(){
			this.payShow = false
			this.confirmShow = true
		},
		// 关闭确认弹窗
		closePopup(){
			this.confirmShow = false
		},
		// 查看解释
		handleToCheckExplain(){
			this.explainShow = true
		},
		// 取消支付 
		handleToClosePay(){
			this.payShow = false
		}
	}
}
